}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Bebas Neue", sans-serif;
  font-weight: normal;
  font-size: 1.675rem;
  line-height: 1.1;
  margin: 0;
}

.detail-screen h1,
.detail-screen h2,
.detail-screen h3,
.detail-screen h4,
.detail-screen h5,
.detail-screen h6 {
  margin: 0 0 1rem;
}

h1 > a,
h2 > a,
h3 > a,
h4 > a,
h5 > a,
h6 > a {
  text-decoration: none;
  color: inherit;
}

p {
  margin: 0 0 1rem;
}

html {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: normal;
}

#root {
  height: 100vh;
  overflow: hidden;
}

.loading-skeleton {
  animation: loading 1.5s infinite;
  background: linear-gradient(0.25turn, transparent, #373737, transparent),
    linear-gradient(#292929, #292929);
  background-position: 0 0;
}

@keyframes loading {
  to {
    background-position: 100vw 0;
  }
}

img {
  max-width: 100%;
}

.content {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

